<template>
    <v-dialog
        :transition="transition"
        :width="width"
        :z-index="zIndex"
        :persistent="persistent"
        v-model="isShowDialog"
    >
        <template v-slot:default="{ isActive }">
            <v-card>
                <v-card-item class="py-2 px-6 text-white text-center" :class="titleClass" v-if="dialog.title">
                    <v-card-title tag="h4" class="text-h6">
                        {{ dialog.title }}
                    </v-card-title>
                </v-card-item>
                <v-card-text :class="contentClass">
                    {{ dialog.message }}
                </v-card-text>
                <v-card-actions :class="actionsClass">
                    <v-spacer/>
                    <v-btn
                        v-if="dialog.button.no"
                        @click.stop="event => handleClickButton(event, false)"
                        :variant="variantButton"
                        :color="noButtonColor"
                        :class="noButtonClass"
                    >
                        {{ dialog.button.no }}
                    </v-btn>
                    <v-btn
                        :variant="variantButton"
                        :color="yesButtonColor"
                        v-if="dialog.button.yes"
                        :class="yesButtonClass"
                        @click.stop="event => handleClickButton(event, true)"
                    >
                        {{ dialog.button.yes }}
                    </v-btn>
                </v-card-actions>
            </v-card>
        </template>
    </v-dialog>
</template>

<script>
import {onMounted, ref} from "vue";
import {events} from "./events";

export default {
    name : "Vuetify3ConfirmDialog",
    props: {
        persistent    : {
            type   : Boolean,
            default: false
        },
        transition    : {
            type   : String,
            default: 'fade-transition'
        },
        variantButton : {
            type   : String,
            default: 'outlined'
        },
        titleClass    : {
            type   : String,
            default: 'bg-blue'
        },
        contentClass  : {
            type: String
        },
        actionsClass  : {
            type: String
        },
        noButtonClass : {
            type: String
        },
        yesButtonClass: {
            type: String
        },
        noButtonColor : {
            type: String
        },
        yesButtonColor: {
            type   : String,
            default: 'blue'
        },
        width         : {
            type   : [String, Number],
            default: 'auto'
        },
        zIndex        : {
            type   : [String, Number],
            default: 2400
        },
    },
    setup() {
        const isShowDialog = ref(false)
        const params       = ref({})
        const dialog       = ref({
            title  : '',
            message: '',
            button : {},
        })
        const resetState        = () => {
            dialog.value = {
                title   : '',
                message : '',
                button  : {},
                callback: () => {
                }
            }
        }
        const handleClickButton = ({target}, confirm) => {
            isShowDialog.value = false
            if (params.value.callback) {
                params.value.callback(confirm)
            }
        }
        const open              = (propsParams) => {
            resetState()
            params.value       = propsParams
            isShowDialog.value = true
            Object.entries(propsParams).forEach(param => {
                if (typeof param[1] === typeof dialog.value[param[0]]) {
                    dialog.value[param[0]] = param[1]
                }
            })
        }
        onMounted(() => {
            if (!document) return
            events.$on('open', open)
        })

        return {
            isShowDialog,
            dialog,
            handleClickButton
        }
    }
}
</script>
